* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    /* outline: 2px red solid; */
}

a {
    text-decoration: none;
}

li {
    list-style: none;
}

body {
    background: #141218;
    background-image: url(../img/backgroundbg_1.jpg);
    background-position: top right 20%;
    background-repeat: no-repeat;
    min-height: 100vh;
    font-family: 'Roboto';
}

.container {
    max-width: 1140px;
    padding: 0 15px;
    margin: 0 auto;
}
/* ===== settings ====== */

/* ===== header Start ====== */
.header__nav {
    display: flex;
    align-items: center;
    padding: 60px 0 90px;
}

.header__nav .nav_item {
    display: flex;
}

.header__nav .nav_item:nth-of-type(1) {
    margin-right: auto;
    margin-left: 67px;
}

.header__nav .nav_item:nth-of-type(1) a{
    margin-right: 24px;
}

.header__nav .nav_item:nth-of-type(2) a{
    margin-left: 20px;
}

.nav_item .nav_link a {
    color: #fff;
    font-size: 14px;
    line-height: 16px;
}

i {
    font-size: 18px;
    background: linear-gradient(45deg, grey 33%, #fff 66%, rgb(121, 115, 115));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    color: #0B2349;
    transition: .5s;
}

i:hover {
    transform: scale(1.2);
}

/* ===== header End ====== */
/* ===== Section 1 start ====== */

.main__content {
    display: flex;
}

.main__genre {
    color: #fff;
    font-size: 14px;
    margin-bottom: 16px;
    display: inline-block;
}

.rating {
    display: flex;
    align-items: center;
}

.rating__number {
    color: #fff;
    font-size: 12px;
    margin-left: 10px;
}

h1 {
    font-size: 76px;
    font-family: 'Mason Chronicles';
    color: #fff;
    font-weight: normal;
    animation: shadow 2s infinite;
}

@keyframes shadow {
    0% {
        text-shadow: 0 0 15px red;
    }
    50% {
        text-shadow: none;
    }
    100% {
        text-shadow: 0 0 15px red;
    }
}

.main__desc {
    max-width: 458px;
    font-size: 14px;
    color: #fff;
    line-height: 150%;
    margin-bottom: 40px;
}

.main__watch {
    display: inline-block;
    text-decoration: none;
    background-color: #9D2929;
    border-radius: 8px;
    color: #fff;
    border: none;
    padding: 22px 72px;
    font-weight: 700;
    transition: .5s;
    cursor: pointer;
}

.main__watch:hover {
    background-color: #892424;
    box-shadow: 0 0 25px 2px #892424;
}

iframe {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: none;
    z-index: 2;
    margin: 20px;
}

.video {
    /* display: none; */
    background-color: rgba(0, 0, 0, 0.8);
    width: 100%;
    height: 100%;
    margin: 0 auto;
    position: fixed;
	  left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    display: none;
}

/* .close-btn {
    font-size: 50px;
    color: red;
    position: fixed;
	  left: 94%;
    top: 0%;
    z-index: 3;
    cursor: pointer;
    display: none;
} */

.active {
    overflow: hidden;
}

.lock {
  opacity: 0;
}

.main__play {
    color: #fff;
    margin: auto;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    background: #9D2929;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    transition: .5s;
}

.main__play:hover {
    background: #892424;
}

@keyframes pulse {
    0% {
        transform: scale(1);
    }
    50% {
        transform: scale(1.1);
    }
    100% {
        transform: scale(1);
    }
}

.main__play::after {
    content: '';
    width: 132px;
    height: 132px;
    background: #9D2929;
    opacity: 0.5;
    position: absolute;
    border-radius: 50%;
    left: -16px;
    top: -16px;
    animation: pulse .8s ease-in-out infinite;
    z-index: -1;
}

.main__play img {
    transform: translateX(5px);
}

/* ===== Section 1 end ====== */

/* ===== slider ===== */

.slider {
    max-width: 500px;
    /* margin: auto; */
}

.slider img {
    width: 100%;
    height: 120px;
    object-fit: cover;
}

.owl-carousel .owl-nav button.owl-next span,
.owl-carousel .owl-nav button.owl-prev span {
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(255, 255, 255, 0.699);
    width: 100%;
    height: 100%;
    background: url(../img/arrow.svg);
    background-repeat: no-repeat;
    background-position: center center;
    color: transparent;
}

.owl-carousel .owl-nav button.owl-prev {
    position: relative;
    margin: 0;
    top: -230px;
    float: left;
    height: 50px;
    display: none;
}
.owl-carousel .owl-nav button.owl-next {
    position: relative;
    margin: 0;
    top: -130px;
    float: right;
    height: 50px;
    left: 50px;
}

.owl-nav > button {
    width: 50px;
    height: 100%;
}

.owl-theme .owl-dots {
    margin-top: -50px;
    display: none;
}

/* .owl-carousel .owl-nav button.owl-prev span:hover,
.owl-carousel .owl-nav button.owl-next span:hover {
    background: #333;
} */

.owl-theme .owl-nav [class*=owl-]:hover {
    /* background: #869791; */
    background: transparent;
    outline: none;
    color: #fff;
    text-decoration: none;
}

.owl-carousel .owl-stage-outer {
    border-radius: 10px;
    margin: 50px 0 60px;
}

.owl-carousel .owl-item img {
    display: block;
    width: 80%;
    border-radius: 10px;
    opacity: 0.5;
}

.chapter {
    position: relative;
}

.chapter span {
    content: '';
    color: #fff;
    position: absolute;
    bottom: 7%;
    left: 7%;
    font-size: 10px;
}

.chapter span b {
    font-size: 14px;
}

/* ===== slider ===== */

/* ===== footer start ===== */

footer {
    background: #141218;
}

.footer__content {
    color: #3A383D;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    position: relative;
    top: -25px;
}

.footer__content .product p {
    margin-top: 5px;
}

.footer__content .product p a {
    color: #9D2929;
    transition: .5s;
}

.footer__content .product p a:hover {
  text-shadow: 0 0 5px red;
  color: #fff;
}

.footer__nav {
    display: flex;
}

.footer__nav li {
    margin-left: 25px;
}

.footer__nav li a {
    color: #3A383D;
    cursor: pointer;
    transition: .5s;
}

.footer__nav li a:hover {
    color: rgba(255, 255, 255, 0.8);
    text-shadow: 0 0 5px red;
}

/* ===== footer end ===== */

/* ===== burger ===== */
