    margin: 0;
    overflow-x: hidden;
    height: 100vh;
    width: 100vw;
  }
  
  ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  .msg {
    font-family: 'Pacifico', cursive;
    font-size: 8vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
    max-height: 100%;
    max-width: 100%;
    align-items: center;
    color: #FEFEFE;
    background-color: #1184F0;
  }
  
  .features {
    font-family: 'Lato', sans-serif;
    font-size: 1rem;
    text-transform: uppercase;
  }
  .features li {
    width: 100%;
    text-align: center;
    padding: .25em;
  }
  .drawer-list {
    position: fixed;
    right: 0;
    top: 0;
    height: 100vh;
    width: 100vw;
    transform: translate(100vw, 0);
    /* ie workaround */
    -ms-transform: translatex(-100vw);
    box-sizing: border-box;
    pointer-events: none;
    padding-top: 125px;
    transition: width 475ms ease-out, transform 450ms ease, border-radius .8s .1s ease;
    border-bottom-left-radius: 100vw;
    /* background-color: #3d88ce;
    background-color: rgba(12, 106, 194, 0.8); */
    background: #9D2929;
    background: rgba(157, 41, 41, 0.8);
  }
  @media (min-width: 768px) {
    .drawer-list {
      width: 40vw;
    }
  }
  .drawer-list ul {
    height: 100%;
    width: 100%;
    margin: 0;
    padding: 0;
    overflow: auto;
    overflow-x: hidden;
    pointer-events: auto;
  }
  .drawer-list li {
    list-style: none;
    text-transform: uppercase;
    pointer-events: auto;
    white-space: nowrap;
    box-sizing: border-box;
    transform: translatex(100vw);
    /* ie workaround */
    -ms-transform: translatex(-100vw);
  }
  .drawer-list li:last-child {
    margin-bottom: 2em;
  }
  .drawer-list li a {
    text-decoration: none;
    color: #FEFEFE;
    text-align: center;
    display: block;
    padding: 1rem;
    font-size: calc(24px - .5vw);
  }
  @media (min-width: 768px) {
    .drawer-list li a {
      text-align: right;
      padding: .5rem;
    }
  }
  .drawer-list li a:hover {
    cursor: pointer;
    /* background-color: #88c2f8;
    background-color: rgba(17, 132, 240, 0.5); */
    background: #000;
    background: rgba(0, 0, 0, 0.5);
  }
  
