/* ===== media ===== */

@media (max-width: 992px) {
    header, main {
        padding: 0 15px;
    }
}

@media (max-width: 768px) {
    .owl-carousel .owl-item img {
        opacity: 1;
    }
    .chapter span {
        text-shadow: 0 0 5px red;
    }
    input.hamburger:checked ~ .drawer-list {
        overflow: auto;
    }
    header, main {
        padding: 0px;
    }
}

@media (max-width: 576px) {
    h1 {
        font-size: 56px;
        margin-bottom: 20px;
    }
    .header__nav .nav_item:nth-of-type(1) {
        display: none;
    }
    .nav_item {
        margin: 0 auto;
    }
    .wtf {
        width: 100%;
        height: 100%;
    }
    .menu {
        display: flex;
      }
      .main__play {
          display: none;
      }
      .owl-carousel .owl-item img {
          width: 85%;
      }
      .chapter span {
          width: 164px;
          left: 7%;
      }
      .main__watch {
          padding: 22px 52px;
